  mounted(){
    // console.log(this.$refs.test);
    EventBus.$on('pMv', (e) => {
      if(e.pressure){
        this.test.wh = new FSPoint(e.offsetX, e.offsetY).offset(new FSPoint(100, 0));
      }else{
        this.test.xy = new FSPoint(e.offsetX, e.offsetY);
      }
      this.touchType = e.pointerType;
    });
  }

  destroyed(){
    EventBus.$off('pMv');
  }
}
