import FSPoint from '@/intern/FSPoint';

interface FSInterface {
  img: ImageBitmap;
  wh: FSPoint;
  xy: FSPoint;
  pivot: FSPoint;
  pts: FSPoint[];
  hidden: boolean;
  size: number;
}

export default class FSObject implements FSInterface {
  public xy = new FSPoint();
  public wh = new FSPoint(100, 100);
  // public wh = new FSPoint(window.innerWidth, window.innerHeight);
  public pivot!: FSPoint;
  public size = 1;
  public pts: FSPoint[] = [];
  public hidden = false;
  public img!: ImageBitmap;

  constructor(obj: object = {}) {
    Object.keys(this).forEach((key: string) => {
      if (this[key] !== obj[key]) {
        this[key] = obj[key] || this[key];
      }
    });

    if (this.pivot == null) {
      this.pivot = new FSPoint(this.wh.x / 2, this.wh.y / 2);
    }
  }

  public snap(pt: FSPoint): FSPoint {
    if (this.size) {
      const x = (Math.floor(pt.x / this.size) * this.size);
      const y = (Math.floor(pt.y / this.size) * this.size);

      return new FSPoint(x,y);
    } else {
      return pt;
    }
  }

  public setPivot(pt: FSPoint) {
    this.pivot = pt;
  }
}
