



import EventBus from '@/EventBus';
import { Component, Vue } from 'vue-property-decorator';

@Component({})
export default class FSEvent extends Vue{
  $refs!: {
    capture: HTMLElement;
  }

  mounted(){
    const cap = this.$refs.capture;

    window.addEventListener('resize', e => EventBus.$emit('resize', e));

    cap.addEventListener('pointerup', e => EventBus.$emit('pUp', e));
    cap.addEventListener('pointerout', e => EventBus.$emit('pOut', e));
    cap.addEventListener('pointerenter', e => EventBus.$emit('pIn', e));
    cap.addEventListener('pointermove', e => EventBus.$emit('pMv', e));
    cap.addEventListener('pointerdown', e => EventBus.$emit('pDn', e));

    /*
    if(EventBus.touch){
      cap.addEventListener('touchstart', (e) => { EventBus.$emit('pUp', e); });
      cap.addEventListener('touchmove', (e) => { EventBus.$emit('pMv', e); });
      cap.addEventListener('touchend', (e) => { EventBus.$emit('pDn', e); });
    } else {
      cap.addEventListener('pointerup', e => EventBus.$emit('pUp', e));
      cap.addEventListener('pointermove', e => EventBus.$emit('pMv', e));
      cap.addEventListener('pointerdown', e => EventBus.$emit('pDn', e));
    }
    */

    cap.addEventListener('dblclick', (e) => { EventBus.$emit('dbl', e); });
    cap.addEventListener('contextmenu', (e) => { e.preventDefault(); EventBus.$emit('context', e); });
  }
}
