  private pressure!: number;
  private eraser = false;

  @Watch('point', {deep: true})
  private pointChange(n, o){
    this.pnts.push(n);
    const lineBuffer = 5;
    if(n && o && this.pnts.length >= lineBuffer + 1){
      /*
      if(typeof this.$refs.can[0].transferControlToOffscreen === "function"){
        this.worker.postMessage({prs: this.pressure, pnts: this.pnts.slice(-lineBuffer, -1)});
