        this.renderer([this.pnts[this.pnts.length - 4], this.pnts[this.pnts.length-1]]);
      }
      */
      this.renderer(this.pnts.slice(-lineBuffer - 1, -1));
      // this.renderer([this.pnts[this.pnts.length - lineBuffer], this.pnts[this.pnts.length-1]]);
    }
  }

  destroyed(){
    EventBus.$off('pIn');
    EventBus.$off('pOut');
    EventBus.$off('pUp');
    EventBus.$off('pDn');
    EventBus.$off('pMv');
  }

  private test(pnts){
    const p1 = new FSPoint(pnts[0].x, pnts[0].y);
    const p2 = new FSPoint(pnts[pnts.length-1].x, pnts[pnts.length-1].y);

    //Slope stuff needs to live on the point object.
    let slopeX, slopeY;
    const buf = 0;
    if(p1.to(p2).rise > buf ){
      slopeY = 1;
    }else if(p1.to(p2).rise < buf){
      slopeY = -1;
    }else{
      slopeY = 0;
    }

    if(p1.to(p2).run > buf ){
      slopeX = -1;
    }else if(p1.to(p2).run < buf){
      slopeX = 1;
    }else {
      slopeX = 0;
    }
    // ===========================================

    this.$refs.can.forEach((can, id) => {
      const ctx = can.getContext('2d') as CanvasRenderingContext2D;

      if(id == 1){
        ctx.clearRect(0, 0, window.innerWidth, window.innerHeight);
        ctx.fillStyle = "#F00";

        ctx.beginPath();
        ctx.lineTo(this.pnts[0].x, p2.y);
        ctx.lineTo(this.pnts[0].x, p2.y);
        ctx.closePath();
        ctx.stroke();
        ctx.beginPath();

        ctx.moveTo(this.pnts[0].x, this.pnts[0].y);
        ctx.lineTo(p2.x, p2.y);
        ctx.moveTo(this.pnts[0].x, this.pnts[0].y);
        ctx.lineTo(p2.x, this.pnts[0].y);
        ctx.moveTo(this.pnts[0].x, this.pnts[0].y);
        ctx.lineTo(this.pnts[0].x, p2.y);
        ctx.moveTo(this.pnts[0].x, this.pnts[0].y);
        ctx.lineTo(this.pnts[0].x + 3, (p2.y - (this.pnts[0].to(p2).dist)) / 2);

        ctx.closePath();
        ctx.stroke();
      }else{
        ctx.fillStyle = "#000";
      }
    });
  }

  private renderer(pnts){
    const ctx = this.$refs.can[0].getContext('2d') as CanvasRenderingContext2D;

    const p1 = new FSPoint(pnts[0].x, pnts[0].y);
    const p2 = new FSPoint(pnts[pnts.length-1].x, pnts[pnts.length-1].y);
    const amt = ((this.pressure * 5) / 5);
    const dist = p1.to(p2).dist * amt;

    // ctx.clearRect(0,0,this.$refs.can[0].width, this.$refs.can[0].height);

    ctx.moveTo(p1.x + dist, p1.y + dist);
    for(let i = 0; i <= pnts.length-1; i++){
      const p3 = new FSPoint(pnts[i].x, pnts[i].y);
      ctx.lineTo(p3.x + dist, p3.y + dist);
    }
    ctx.lineTo(p2.x + dist, p2.y + dist);
    ctx.lineTo(p2.x - dist, p2.y - dist);
    for(let i = 0; i <= pnts.length-1; i++){
      const p3 = new FSPoint(pnts[i].x, pnts[i].y);
      ctx.lineTo(p3.x - dist, p3.y - dist);
    }
    ctx.lineTo(p1.x - dist, p1.y - dist);

    ctx.closePath();
    ctx.fill();
    ctx.strokeStyle = "#F00";
    // ctx.stroke();

    /*
    // const dist = [1];
    const dist = [p1.to(p2).dist * amt];

    ctx.moveTo(p1.x + dist[0], p1.y + dist[0]);
    ctx.lineTo(p2.x + dist[0], p2.y + dist[0]);
    ctx.lineTo(p2.x - dist[0], p2.y - dist[0]);
    ctx.lineTo(p1.x - dist[0], p1.y - dist[0]);
    ctx.closePath();
    // ctx.stroke();
    ctx.fill();
    */

