        p4 = new FSPoint(pnts[i - (max + 1)].x, pnts[i - (max + 1)].y);
        p3 = new FSPoint(pnts[i].x, pnts[i].y);
        dist[i] = 10;
        ctx.lineTo(pnts[i].x - dist[i], pnts[i].y - dist[i]);
