          ctx.lineTo(pnts[j].x + dist[j], pnts[j].y + dist[j]);
        }
      }
    }
    */


  }

  mounted(){
    this.$refs.can.forEach(can => {
      can.width = window.innerWidth;
      can.height = window.innerHeight;
    });

    document.addEventListener('keyup', (e) => {
      if(e.which == 82){
        this.eraser = !this.eraser;
      }
    })

    if(typeof this.$refs.can[0].transferControlToOffscreen === "function"){
      // this.offscreen = this.$refs.can[0].transferControlToOffscreen();
      // this.worker = OffWorker({type: module});
      // this.worker.postMessage({canvas: this.offscreen}, [this.offscreen]);
      // console.log(this.worker)
    }

    EventBus.$on('pIn', (e) => {
      // this.pnts.push(new FSPoint(e.offsetX, e.offsetY));
    });

    EventBus.$on('pOut', (e) => {
      this.pnts.push(new FSPoint(e.offsetX, e.offsetY));
      this.pnts = [];
      this.point = null;
    });

    EventBus.$on('pUp', (e) => {
      this.pnts = [];
      this.point = null;
    });

    EventBus.$on('pMv', (e) => {
      if(e.pressure){
        // this.point = new FSPoint(e.offsetX, e.offsetY);
        this.pressure = e.pressure;
        this.point = new FSPoint(Math.floor(e.clientX), Math.floor(e.clientY));
      }else{
        this.pnts = [];
        this.point = null;
      }
    });


    this.$nextTick(() => {
      //
    });
  }
