



import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
// import EventBus from '@/EventBus';
import FSObject from '../intern/FSObject';
import FSPoint from '../intern/FSPoint';

@Component
export default class FSBase extends Vue {
  $el!: HTMLElement;

  @Prop ({ default: () =>  new FSObject() }) public FSObject !: FSObject;
  @Prop ({ default: true }) public locked !: boolean;
  @Prop ({ default: true }) public snapped !: boolean;

  protected setPivot(pt) {
    this.FSObject.setPivot(pt);
  }

  protected setWH(pt: FSPoint = new FSPoint()) {
    this.$nextTick(function() {
      const ipt = this.FSObject.snap(pt);
      if (ipt !== undefined) {
        const el = this.$el as HTMLElement;
        el.style.width = ipt.x + 'px';
        el.style.height = ipt.y + 'px';
      }
    });
  }

  protected setXY(pt: FSPoint = new FSPoint()) {
    const ipt: FSPoint = pt.offset( this.FSObject.pivot, -1 );
    let ept = new FSPoint();

    const min = new FSPoint();
    const max = new FSPoint(
      window.innerWidth,
      window.innerHeight
    ).offset( this.FSObject.wh, -1 );

    if (this.locked) {
      if (ipt.y <= max.y && ipt.y >= min.y) {
        ept.y = ipt.y;
      } else {
        ept.y = ipt.y >= min.y ? max.y : min.x;
      }
      if (ipt.x <= max.x && ipt.x >= min.x) {
        ept.x = ipt.x;
      } else {
        ept.x = ipt.x >= min.x ? max.x : min.x;
      }
    } else {
      // ept = this.FSObject.setXY(ipt);
      ept = ipt;
    }

    if(this.snapped){
      ept = this.FSObject.snap(ept);
    }

    this.$nextTick(function() {
      const el = this.$el as HTMLElement;
      el.style.top = ept.y + 'px';
      el.style.left = ept.x + 'px';
    });

  }

  mounted(){
    this.$nextTick(() => {
      this.setWH(this.FSObject.wh);
    });
  }

  @Watch('FSObject', { immediate: true, deep: true })
  protected fsUpdate(obj: FSObject) {
    this.setXY(obj.xy);
    this.setWH(obj.wh);
    this.setPivot(obj.pivot);
  }
}
