






import EventBus from '@/EventBus';
import {Vue, Component} from 'vue-property-decorator';

@Component
export default class FSDebug extends Vue{
  private pointerType = "";

  mounted(){
    EventBus.$on('pMv', e => {
      this.pointerType = e.pointerType;
    })
  }

  destroyed(){
    EventBus.$off('pMv');
  }
}
